html{
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-size:100%;
  background-position: bottom;
  background-color: #333031;
  min-height: 100%;
}

form{
  padding:20px;
  margin-top: 60px;
  border-radius: 20px;
}

form button, form h5{
  margin:20px 10px;
}

.circular_img{
  border: 3px solid white;
  border-radius: 50%;
  margin-bottom: 10px;
  width:200px;
  height:200px;
}

.frontpage{
  margin-top: 20px;
}

.profile{
  margin-top: 50px;
  padding: 20px;
  background: white;
  border-radius: 20px;
}

.font-bold{
  font-weight:bold;
}

.text-white{
  color: white;
}

.car-img{
  width:auto;
  height:200px;
}

h5{
  font-weight:bold;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #e7a411;
  box-shadow: none;
}

